.site-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  visibility: hidden;
}

.overlay-link {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 10;
}

/* Slider */
.site-slider .owl-nav > * {
  position: absolute;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 2.25rem !important;
  width: 1.875rem;
  height: 2.25rem;
  top: 50%;
  z-index: 1;
}
.site-slider .owl-nav > *.owl-next {
  right: 0;
}
.site-slider .owl-nav > * > i {
  font-size: inherit;
  margin: 0;
  top: 0;
}
.site-slider .owl-nav > * > i::before {
  margin-left: 0;
  margin-right: 0;
}
.site-slider .owl-dots {
  display: flex;
  align-items: center;
  justify-content: center;
}
.site-slider .owl-dots button {
  width: 0.625rem;
  height: 0.625rem;
  background-color: transparent !important;
  margin-right: 0.25rem;
}
@media screen and (min-width: 48rem) {
  .site-slider .owl-dots button {
    margin-right: 0.375rem;
  }
}
.site-slider .owl-dots button:last-child {
  margin-right: 0;
}
.site-slider .owl-dots button span {
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  background-color: currentColor;
  border-radius: 50%;
  opacity: 0.3;
  will-change: opacity;
  transition: opacity 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
}
.site-slider .owl-dots button.active span, .site-slider .owl-dots button:hover span {
  opacity: 1;
}
.site-slider.carousel .owl-dots {
  margin-top: 2.5rem;
}
@media screen and (min-width: 75rem) {
  .site-slider.carousel .owl-nav > * {
    opacity: 0.15;
    will-change: opacity;
    transition: opacity 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
  }
  .site-slider.carousel .owl-nav > *:hover {
    opacity: 0.5;
  }
  .site-slider.carousel .owl-prev {
    left: -2.5rem;
  }
  .site-slider.carousel .owl-next {
    right: -2.5rem;
  }
}
.site-slider.slider .owl-dots {
  margin-top: 0.75rem;
}
@media screen and (min-width: 64.0625rem) {
  .site-slider.slider .owl-dots {
    position: absolute;
    width: 100%;
    bottom: 1.25rem;
  }
  .site-slider.slider .owl-dots button span {
    background-color: #fff;
  }
}
.site-slider.arrow-1 .owl-prev, .site-slider.arrow-1 .owl-next {
  font-size: 1.4375rem !important;
  text-shadow: 0px 0px 0px;
  width: 2rem;
  height: 3.5rem;
  background-color: #fff !important;
  opacity: 0;
  transition: opacity 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
}
.site-slider.arrow-1:hover .owl-prev, .site-slider.arrow-1:hover .owl-next {
  opacity: 1;
}

.countdown {
  display: flex;
}

.page-header-image {
  position: relative;
  min-height: 9.375rem;
  background-size: cover;
  background-position: center center;
}
@media screen and (min-width: 64.0625rem) {
  .page-header-image {
    min-height: 28.75rem;
  }
}

/* page not found */
.page-not-found {
  text-align: center;
  background-size: cover;
  background-position: center center;
  padding-top: 2.5rem;
  padding-bottom: 1.5rem;
}
@media screen and (min-width: 48rem) {
  .page-not-found {
    padding-top: 12.5rem;
    padding-bottom: 16.5rem;
  }
}
.page-not-found .entry-title {
  font-size: 3.75rem;
  font-weight: 700;
  line-height: 1;
}
@media screen and (min-width: 48rem) {
  .page-not-found .entry-title {
    font-size: 15.625rem;
  }
}
.page-not-found .entry-subtitle {
  font-size: 1.25rem;
  font-weight: 600;
}
@media screen and (min-width: 48rem) {
  .page-not-found .entry-subtitle {
    font-size: 2.25rem;
  }
}
.page-not-found .entry-content {
  max-width: 25rem;
  margin-left: auto;
  margin-right: auto;
  color: var(--color-desc);
}

/* Quickview */
.quickview-product .mfp-close {
  color: #111 !important;
  background-color: #fff !important;
  border: 1px solid var(--color-border);
  line-height: 35px;
}
.quickview-product .preloader {
  transition: all 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
}
.quickview-product-wrapper {
  position: relative;
}
.quickview-product-wrapper .single-thumbnails {
  position: relative;
  height: 2.5rem;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  transition: all 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
}
.quickview-product.loaded .single-thumbnails {
  height: auto;
  opacity: 1;
  visibility: visible;
}
.quickview-product.loaded .preloader {
  opacity: 0;
  visibility: hidden;
}

/* gdpr */
.gdpr-content {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  padding: 0.9375rem 0;
  color: #fff;
  background-color: #000;
  z-index: 1001;
  opacity: 0;
  transform: translateY(100%);
  will-change: opacity, transform;
}
@media screen and (min-width: 64.0625rem) {
  .gdpr-content {
    max-width: 340px;
    bottom: 0.9375rem;
    left: 0.9375rem;
    padding: 1.875rem;
  }
  .gdpr-content .container {
    padding: 0;
  }
}
.gdpr-content .gdpr-inner {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row wrap;
}
@media screen and (min-width: 64.0625rem) {
  .gdpr-content .gdpr-inner {
    flex-direction: column;
    align-items: flex-start;
  }
}
.gdpr-content p {
  font-size: 0.875rem;
  margin-bottom: 0;
}
.gdpr-content a {
  font-size: 0.875rem;
  height: 2.125rem;
  margin-left: 0.625rem;
  border-radius: 0;
}
@media screen and (min-width: 64.0625rem) {
  .gdpr-content a {
    margin-left: 0;
    margin-top: 0.9375rem;
  }
}

.newsletter-popup {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
}
.newsletter-popup .newsletter-close {
  position: absolute;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 2.125rem;
  height: 2.125rem;
  background-color: #fff;
  border-radius: 50%;
  top: 0.625rem;
  right: 0.625rem;
  cursor: pointer;
  z-index: 1;
}
.newsletter-popup .newsletter-popup-inner {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 780px;
  width: 100%;
  background-color: #fff;
}
@media screen and (min-width: 48rem) {
  .newsletter-popup .newsletter-popup-inner {
    flex-direction: row;
  }
}
.newsletter-popup .newsletter-popup-inner .column {
  display: inline-flex;
  align-items: center;
  flex: 1;
}
.newsletter-popup .newsletter-content {
  text-align: center;
  padding: 1.875rem;
}
@media screen and (min-width: 48rem) {
  .newsletter-popup .newsletter-content {
    padding: 3.75rem 2.5rem;
  }
}
.newsletter-popup .newsletter-content .entry-title {
  font-size: 1.25rem;
}
@media screen and (min-width: 48rem) {
  .newsletter-popup .newsletter-content .entry-title {
    font-size: 1.5rem;
  }
}
.newsletter-popup .newsletter-content form input {
  text-align: center;
  height: 48px;
}
.newsletter-popup .newsletter-content form button {
  width: 100%;
  height: 48px;
  margin-top: 5px;
}
.newsletter-popup .newsletter-image {
  position: relative;
  display: block;
  width: 100%;
  min-height: 300px;
  overflow: hidden;
}
@media screen and (min-width: 48rem) {
  .newsletter-popup .newsletter-image {
    min-height: 490px;
  }
}
.newsletter-popup .newsletter-image img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

/* mobile bottom menu */
.mobile-bottom-menu {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  color: #8a8b8e;
  background-color: #fff;
  border-top: 1px solid var(--color-border);
  padding: 0.625rem 1.25rem;
  z-index: 1010;
}
.mobile-bottom-menu .mobile-menu ul {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}
.mobile-bottom-menu .mobile-menu ul li {
  flex: 1;
  padding-left: 5px;
  padding-right: 5px;
}
.mobile-bottom-menu .mobile-menu ul li a {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 0.5625rem;
  font-weight: 600;
  text-transform: uppercase;
  color: currentColor;
  text-decoration: none;
}
.mobile-bottom-menu .mobile-menu ul li a i {
  font-size: 1.375rem;
}
.mobile-bottom-menu .mobile-menu ul li a.search-button::before, .mobile-bottom-menu .mobile-menu ul li a.search-button::after {
  width: 1.375rem;
  top: 0.9375rem;
}
.mobile-bottom-menu .mobile-menu ul li.active > a {
  color: var(--color-primary);
}

.categories-holder {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #fff;
  transition: all 0.3s cubic-bezier(0.28, 0.12, 0.22, 1);
  opacity: 0;
  visibility: hidden;
  z-index: 100;
}
.categories-holder .categories-inner {
  position: relative;
  width: 100%;
  height: 100%;
  padding-bottom: 4.25rem;
}
.categories-holder ul {
  width: 100%;
  height: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
}
.categories-holder ul li + li {
  border-top: 1px solid var(--color-border);
}
.categories-holder ul li a {
  position: relative;
  display: flex;
  align-items: center;
  color: currentColor;
  text-decoration: none;
  padding: 0.625rem 0.9375rem;
}
.categories-holder ul li a .count {
  font-size: 0.75rem;
  margin-left: auto;
  padding-left: 0.75rem;
  opacity: 0.4;
}

.search-holder {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s cubic-bezier(0.28, 0.12, 0.22, 1);
  z-index: 10;
}
.search-holder .container {
  position: relative;
  padding-top: 1.25rem;
}
@media screen and (min-width: 48rem) {
  .search-holder .container {
    padding-top: 1.875rem;
  }
}
.search-holder-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.625rem;
}
.search-holder-header > span {
  font-size: 0.875rem;
}
@media screen and (min-width: 48rem) {
  .search-holder-header > span {
    font-size: 1rem;
  }
}
.search-holder-header .search-holder-close {
  cursor: pointer;
}
.search-holder .search-form {
  position: relative;
  margin-bottom: 0.75rem;
}
@media screen and (min-width: 48rem) {
  .search-holder .search-form input {
    height: 3.5rem;
  }
}
.search-holder .search-form button {
  position: absolute;
  height: 100%;
  top: 0;
  right: 0;
  background-color: transparent;
  border: 0;
  color: currentColor;
}
.search-holder .search-form button i {
  margin-left: 0;
}
.search-holder .search-holder-message > * {
  font-size: 0.875rem;
  opacity: 0.4;
}
.search-holder .search-holder-tags {
  border-top: 1px solid var(--color-border);
  margin-top: 1.25rem;
  padding-top: 1.25rem;
}
.search-holder .search-holder-tags > span {
  display: block;
  font-size: 0.875rem;
  margin-bottom: 0.5rem;
}
.search-holder .search-holder-tags ul {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}
.search-holder .search-holder-tags ul li {
  margin-right: 0.25rem;
  margin-bottom: 0.125rem;
  will-change: opacity, transform;
}
.search-holder .search-holder-tags ul li:last-child {
  margin-right: 0;
}
.search-holder .search-holder-tags ul li a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 500;
  text-decoration: none;
  color: currentColor;
  height: 2rem;
  padding: 0 0.625rem;
  border: 1px solid #eaeaea;
  border-radius: 2px;
  transition: all 0.2s cubic-bezier(0.28, 0.12, 0.22, 1);
  backface-visibility: hidden;
  transform: translateZ(0);
  -webkit-font-smoothing: subpixel-antialiased;
}
.search-holder .search-holder-tags ul li a:hover {
  opacity: 0.6;
}

body.search-active {
  overflow: hidden;
}
body.search-active .search-holder {
  opacity: 1;
  visibility: visible;
}
body.search-active .search-button::before, body.search-active .search-button:after {
  content: "";
  position: absolute;
  width: 1.25rem;
  height: 1px;
  background-color: currentColor;
}
body.search-active .search-button::before {
  transform: rotate(45deg);
}
body.search-active .search-button::after {
  transform: rotate(-45deg);
}
body.search-active .search-button i {
  opacity: 0;
}
body.search-active.header-sticky-active .search-holder .search-holder-inner {
  padding-top: 4.375rem !important;
}
body.categories-active .categories-holder {
  opacity: 1;
  visibility: visible;
}
body.categories-active .mobile-bottom-menu .categories-button::before, body.categories-active .mobile-bottom-menu .categories-button:after {
  content: "";
  position: absolute;
  width: 1.375rem;
  height: 1px;
  top: 0.9375rem;
  background-color: currentColor;
}
body.categories-active .mobile-bottom-menu .categories-button::before {
  transform: rotate(45deg);
}
body.categories-active .mobile-bottom-menu .categories-button::after {
  transform: rotate(-45deg);
}
body.categories-active .mobile-bottom-menu .categories-button i {
  opacity: 0;
}

/*------------------------------------ 
    04 - Icon Font 
------------------------------------*/
@font-face {
  font-family: "klbtheme";
  src: url("../fonts/klbtheme.eot?55270055");
  src: url("../fonts/klbtheme.eot?55270055#iefix") format("embedded-opentype"), url("../fonts/klbtheme.svg?55270055#klbtheme") format("svg");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "klbtheme";
  src: url("data:application/octet-stream;base64,") format("woff"), url("data:application/octet-stream;base64,") format("truetype");
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
/*
@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: 'klbtheme';
    src: url('../font/klbtheme.svg?55270055#klbtheme') format('svg');
  }
}
*/
[class^=klbth-icon-]:before, [class*=" klbth-icon-"]:before {
  font-family: "klbtheme";
  font-style: normal;
  font-weight: normal;
  speak: never;
  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  /* opacity: .8; */
  /* For safety - reset parent styles, that can break glyph codes*/
  font-variant: normal;
  text-transform: none;
  /* fix buttons height, for twitter bootstrap */
  line-height: 1em;
  /* Animation center compensation - margins should be symmetric */
  /* remove if not needed */
  margin-left: 0.2em;
  /* you can be more comfortable with increased icons size */
  /* font-size: 120%; */
  /* Font smoothing. That was taken from TWBS */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Uncomment for 3D effect */
  /* text-shadow: 1px 1px 1px rgba(127, 127, 127, 0.3); */
}

