.widget-checkbox-list ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.widget-checkbox-list ul li {
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-flow: row wrap;
}
.widget-checkbox-list ul li + li {
  margin-top: 0.5rem;
}
